<template>
  <router-view id="app"/>
</template>

<script>
import axios from "axios";
export default {
  created() {
    axios
      .get("/character")
      .then(res => {
        this.$store.commit("Roster/setData", res.data);
      })
      .catch(err => {
        console.log(err);
      });
    this.$store.commit("user/SET_USER", {
      _id: 1,
      userName: Math.random()
        .toString(36)
        .replace(/[^a-z]+/g, "")
        .substr(0, 5),
      isLoggedIn: true
    });
  }
};
</script>

