import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: "/selection",
    name: "selection",
    component: ()=> import(/* webpackChunkName: "game-group" */ '../views/SelectionScreen')
  },
  {
    path: "/ingame",
    name: "ingame",
    component: ()=> import("../views/IngameScreen") 
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
