export default {
    namespaced: true,
    state: {
        memberRank: '',
        userName: '',
        id: '',
        isLoggedIn: false,
      },
      mutations: {
        SET_USER: function (state, payload) {
          state.id = payload._id;
          state.userName = payload.userName;
          state.memberRank = payload.memberRank;
          state.isLoggedIn = true;
        }
      },
      getters: {
        getUserData: (state)=>{
          return {
            id: state.id,
            userName: state.userName,
            memberRank: state.memberRank
          }
        }
      },
      actions: {
      },
}